@import url(https://fonts.googleapis.com/css?family=Montserrat|Noto+Serif+SC);
#app {
  font-family: 'Noto Serif SC', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
#container {
  background: #000000;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0px;
  left: 0px;
  color: white;
  z-index: -1;
}
#loading {
  width: 100vw;
  height: 100vh;
  z-index: 20;
  display: fixed;
  background: black;
}
#title-text {
  width: 100vw;
  height: 100vh;
  z-index: 10;
  position: absolute;
  top: 0px;
  left: 0px;
  background: transparent;
}

#app-container {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: white;
  margin-top: 60px;
}
#landing-page {
}
.intro {
  padding: 30vh;
}
.step {
  /* padding: 5vh 0; */
  width: 100vw;
  height:100vh;
  left:0px;
  top:0px;
  margin: 0 auto;
  background-color: rgba(0,0,0,0.35);
  border: 1px solid #ccc;
  /* z-index: 10; */
}
.step.active {
  background-color: rgba(0,0,0,0.4);
  color: white;
}
